import { INCREMENT, DECREMENT } from './actionTypes';

/**
 * Action: const INCREMENT = { type: INCREMENT, payload: { plugin: plugin } }
 * Action Creator function returns action: const increment = (plugin) => return { type: INCREMENT, payload: { plugin: plugin } }
 * Dispatch by passing action creator function: dispatch(increment(plugin))
 * Reducer specifies state change logic
 */

export const decrement = plugin => {
  console.log(`src/state/actions.js:decrement`);
  console.log(plugin);
  return {
    type: DECREMENT,
    payload: { plugin: plugin }
  };
};

export const increment = plugin => {
  console.log(`src/state/actions.js:increment`);
  console.log(plugin);
  return {
    type: INCREMENT,
    payload: { plugin: plugin }
  };
};

