import { INCREMENT, DECREMENT } from '../actionTypes';

import initialState from 'state/initialState';

export const plugins = (state = initialState, action) => {
  console.log(`src/state/reducers/index.js:plugins`);
  console.log(`action`);
  console.log(action);
  console.log(`state`);
  console.log(state);

  switch (action.type) {
    case INCREMENT:
      const { plugins } = state;
      const actionPlugin = action.payload.plugin;
      const statePluginIndex = plugins.findIndex(
        plugin => plugin.id === actionPlugin.id
      );
      const statePlugin = plugins[statePluginIndex];

      const diff =
        plugins[statePluginIndex].state.adjustment +
        plugins[statePluginIndex].state.increment;

      plugins[statePluginIndex].state.adjustment =
        diff > plugins[statePluginIndex].state.maximum
          ? plugins[statePluginIndex].state.maximum
          : diff;

      // return { plugin: statePlugin };

      return Object.assign({}, state, { plugins: plugins });
    default:
      return state;
  }
};

export default plugins;
