// @flow
import type { IPlugin } from 'plugins/base';
import type { IScalable } from 'plugins/scalable';

/**
 * Singleton class that handles active Plugins across app.
 */
class PluginManager {
  static instance: PluginManager;
  #plugins: Array<IPlugin> = [];

  constructor(params?: { plugins: Array<IPlugin> }) {
    if (!!PluginManager.instance) {
      return PluginManager.instance;
    }
    PluginManager.instance = this;

    if (params) {
      this.#plugins = params.plugins;
    }

    return this;
  }

  add(plugin: IPlugin) {
    if (this.exists(plugin.id)) {
      throw new Error(
        `Cannot add Plugin with ID: ${plugin.id} - Matching ID already exists.`
      );
    }
    this.plugins.push(plugin);
  }

  delete(plugin: string | IPlugin) {
    const index = this.findIndex(plugin);
    if (index === -1) return;
    delete this.plugins[index];
  }

  exists(id: string | IPlugin): boolean {
    return !!this.find(id);
  }

  find<T: any>(id: string | IPlugin): any {
    if (typeof id !== 'string') {
      // Get underlying ID
      id = id.id;
    }
    // if (!this.plugins) return void;

    // return this.plugins.find(plugin => plugin.id === id);
    const index = this.plugins.findIndex(plugin => plugin.id === id);
    return this.plugins[index];
  }

  findIndex(id: string | IPlugin): number {
    if (typeof id !== 'string') {
      id = id.id;
    }
    return this.plugins.findIndex(plugin => plugin.id === id);
  }

  get(id: string | IPlugin): IPlugin | void {
    return this.find(id);
  }

  get initialState() {
    return this.plugins.map<any>(plugin => plugin.initialState);
  }

  get plugins() {
    return this.#plugins;
  }
}

const pluginManagerInstance = new PluginManager();

export default pluginManagerInstance;
